<template>
    <div id="login">
        <div class="list">
            <h1>Login</h1>
            <p>Login to keep track of your todos. This will create a new user if you are new to the system.</p>
            <input type="text" name="username" v-model="input.username" placeholder="Username"/>
            <input type="password" name="password" v-model="input.password" placeholder="Password"/>
            <input type="password" name="verify_password" v-model="input.verifyPassword" placeholder="Verify Password"/>
            <button type="button" @click="login()">Login</button>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Login',
        data() {
            return {
                input: {
                    username: "",
                    password: "",
                    verifyPassword: "",
                }
            }
        },
        methods: {
            login: function () {
                if (this.input.username !== "" && this.input.password !== "" && this.input.verifyPassword !== "") {
                    if (this.apiLogin()) {
                        this.$emit("authenticated", true, this.input.username, this.input.password);
                        this.$router.replace({name: "todos"});
                    } else {
                        console.log("The username and / or password is incorrect");
                    }
                } else {
                    console.log("A username and password must be present");
                }
            },
            apiLogin: function () {
                const options = {
                    method: 'post',
                    headers: {
                        'Content-type': 'application/x-www-form-urlencoded; charset=UTF-8'
                    },
                    body: `username=${this.username}&email=''&password=${this.password}&verify_password=${this.verifyPassword}`
                };
                let userInfo;

                fetch('/api/v1/users', options)
                    .then(response => response.json())
                    .then(jsonData => (userInfo = jsonData))
                    .catch(err => {
                        console.error('Request failed', err);
                    });

                console.log(userInfo);

                return !userInfo.error;
            }
        }
    }
</script>
